css *,
*::before,
*::after,
:root {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  color: #252934;
  background: #fafafa;
  font-size: 62.5%;
  font-family: 'Overlock', Arial, Helvetica, sans-serif;
  overflow-x: hidden;
}

a,
a:visited {
  color: #252934;
  font-size: 1.4rem;
  text-decoration: none;
  transition: 200ms;
}

a:hover,
a:active {
  color: #f300b4;
}

button,
button:visited {
  width: 100%;
  max-width: 240px;
  padding: 1rem;
  border: 1px solid #fafafa;
  color: #fafafa;
  text-align: center;
  text-transform: uppercase;
  font-size: 1rem;
  transition: all ease-in-out 0.25s;
  cursor: pointer;
  box-sizing: border-box;
}

button,
button:visited {
  background: #f300b4;
  border: 1px solid transparent;
  color: #fafafa;
  font-weight: bold;
  outline: none;
}

button:hover {
  background: transparent;
  border: 1px solid #f300b4;
  color: #f300b4;
  transition: all ease-in-out 0s;
  outline: none;
}

button:active {
  background: transparent;
  border: 1px solid #f300b4;
  color: #f300b4;
  transition: all ease-in-out 0s;
  outline: none;
  padding: 1.05rem 1rem calc(0.95rem - 1px) calc(1rem - 1px);
  border-top: 2px solid;
  border-right: 2px solid;
}

button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

button:disabled:hover {
  background: #f300b4;
  border: 1px solid transparent;
  color: #fafafa;
}

